export default {
  name: 'LandingExchange',
  props: {
    title: {
      type: String,
      default: '',
    },
    name: {
      type: String,
      require: true,
      default: '',
    },
    defaultValue: {
      type: [Number, String],
      default: '',
    },
    exchangeSelected: {
      type: Object,
      required: true,
    },
    exchanges: {
      type: Array,
      required: true,
    },
    type: {
      type: String,
      default: 'text',
    },
  },
  data() {
    return {
      openDropdown: false,
    }
  },
  methods: {
    toggleDropdown() {
      this.openDropdown = !this.openDropdown
    },
  },
}
